@media screen and (max-width: 991px) {
    section {
        margin-bottom: $section-margin;
        &.container-fluid {
            padding: 55px 20px 60px;
            position: relative;

            &:before {
                left: 0;
                right: 0;
            }

            > .container {
                padding-left: 0;
                padding-right: 0;
            }
        }
    }

    .container {
        padding-left: 20px;
        padding-right: 20px;
    }
    .row {
        margin-left: -20px;
        margin-right: -20px;
    }
    .navbar {
        padding-top: 16px;
        padding-bottom: 18px;
        &-brand { 
            img {
                max-width: 126px;
            }
        }
        
        &-collapse {
            background-color: $bg-secondary;
            display: flex;
            flex-direction: column;
            margin: 0 -20px;
            padding: 0 40px;
            transform: translate(0, 15px);
        }
        &-nav {
            padding: 37px 0 23px;
            a {
                padding: 0 0 11px;
                font-size: 24px;
                line-height: 222%;
            }
        }
        .btn {
            padding: 22px 26px;
            margin: 0 auto 60px;
            width: 100%;
            max-width: 295px;
            justify-content: center;
        }
    }
    .first-screen {
        .container {
            padding: 0 28px;
            text-align: center;
            z-index: 1;
        }
        .container-xxl {
            min-height: calc(100vh - 74px);
            position: relative;
            
            &::after {
                content: "";
                position: absolute;
                background-color: rgba(0, 0, 0, 0.363);
                top: 0;
                right: 0;
                bottom: 0;
                left: 0;
            }
        }
        &-slider__title {
            font-size: 32px;
            margin-bottom: 10px;
        }
        &-slider__subtitle {
            font-size: 16px;
            margin-bottom: 18px;
        }
        .btn-lg {
            white-space: nowrap;
            .icon {
                margin: 0 -14px 0 35px;
            }
        }
        .slick-dots {
            bottom: 62px;
        }
    }
    .calculation {
        &__img {
            display: none;
        }
    }
    .template-title {
        margin-bottom: 35px;
        h2 {
            font-size: 24px;
            font-weight: 700;
            line-height: 124.5%;
        }
        &.col-lg-7.text-left {
            text-align: center;
            h2 {
                &:after{
                    display: none;
                }
            }
            &:after {
                left: 50%;
                top: calc(100% - 2px);
                transform: translate(-50%, 0);
                display: block;
            }
        } 
    }

    .advantages {
        .slick-slider {
            padding-top: 10px;
        }
        .slick-dots {
            margin-bottom: 60px;
        }
    }

    .advantage {
        padding: 0 20px;
        margin-bottom: 25px;
    }

    .col-lg-3:nth-child(n+2)  {
        margin-top: 0px;
    }

    // description
    .description {
        // display: flex;
        // justify-content: center;
        max-width: 598px;
    }

    
    .description-text {
        text-align: justify;
        font-size: 16px;
        line-height: 149.5%;
        margin: -9px 9px 30px;
    }
    
    .description__img-box {
        position: relative;
        margin: auto;
    }

    .description__img {
        width: 90vw;
    }

    // questions
    .questions {
        margin-bottom: 125px;
        h2 {
            display: flex;
            justify-content: center;
        }
        .accordion-item {
            max-width: 100%;
        }
        .questions-form {
            margin-left: 0;
        }
        .icon {
            display: inline-block;
        }
        
    }

    .cooperation {
        .template-title {
            margin-bottom: 21px;
        }
        h3 {
            font-size: 16px;
        }
        .row {
            display: flex;
            flex-direction: column-reverse;
            align-items: center;
        }
        &-img {
            display: flex;
            justify-content: center;
            margin-right: 0;
        }
        li {
            text-align: justify;
            font-size: 16px;
            line-height: 149.5%;
            margin-bottom: 22px;
        }
        
        .img-fluid {
            max-width: 1000px;
            margin-bottom: -32px;
        }
        .second-row {
            flex-direction: column;
            .cooperation-img-2 {
                margin-left: 0;
                margin-bottom: -35px;
            }
            li {
                margin: 0 0 21px 0;
            }
            button {
                max-width: 100%;
                width: 335px;
                margin-top: 5px;
                // padding: 21px 146px 20px 31px;
                margin-left: 24px;
                position: relative;
                & .icon {
                    position: absolute;
                    top: 18px;
                    right: 34px;
                }
            }
        }
    }
}


