.cooperation {

    & .img-fluid {
        max-width: 635px;
    }
    ul {
        list-style: none;
    }
    li {
        font-size: 17px;
        font-weight: 300;
        line-height: 156.5%;
        margin-bottom: 29px;
    }

    &-description {
        margin: 34px 0px 43px -32px;
    }

    &-img {
        border-radius: 20px;
        margin-top: 9px;
        margin-right: -44px;
    }

    & .second-row {
        & .img-fluid {
            margin-left: -35px;
        }
        li {
            margin: 26px 14px 28px 19px;
        }

        & button {
            margin-top: 13px;
            margin-left: 51px;
        }
    }
}