.questions {
    .accordion-item {
        max-width: 716px;
        margin-bottom: 17px;
        border-radius: 10px;
        border: 1px solid #3A444E;
        background-color: $bg-primary;
    }

    .accordion-button {
        min-height: 61px;
        border-radius: 10px;
        background-color: transparent;
        color: #fff;
        font-size: 18px;
        font-weight: 700;
        padding: 8px 34px;
        line-height: 124.5%;
    }

    .accordion-button:focus {
        box-shadow: none;
    }

    .accordion-button::after {
        content: "";
        background-image: url(assets/img/arrow-circle-left\ 1.svg);
        margin-right: -16px;
        background-size: cover;
        height: 27px;
        width: 27px;
        transition: 0.5s;
    }

    .accordion-button:not(.collapsed)::after {
        background-image: url(assets/img/arrow-circle-left\ 2.svg);
        // margin-right: -16px;
        transform: rotate(361deg);
    }

    .accordion-button:not(.collapsed) {
        color: #282B38;
        font-size: 18px;
        font-weight: 700;
        
        border-radius: 10px;
        background: #fff;
        border-bottom-left-radius: 0px;
        border-bottom-right-radius: 0px;
        box-shadow: none;
    }

    .accordion-body {
        font-size: 17px;
        font-weight: 300;
        background-color: #fff;
        border-radius: 10px;
        border-top-left-radius: 0px;
        border-top-right-radius: 0px;
        color: black;
        color: #282B38;
        padding: 0px 34px 14px;
    }

    // form
    .questions-form {
        background-color: $bg-secondary;
        border-radius: 20px;
        padding: 38px 40px;
        margin-left: -16px;
    }

    .questions__form-title {
        color: #FFF;
        font-size: 24px;
        font-weight: 700;
        line-height: 126%;
        margin-bottom: 8px;
    }

    .questions__form-text {
        color: #FFF;
        font-size: 17px;
        font-style: normal;
        font-weight: 300;
        line-height: 126%;
        margin-bottom: 23px;
    }

    ::placeholder {
        color: #CBCBCB;
        font-size: 15px;
        line-height: 126%;
    }

    textarea {
        border-radius: 10px;
        padding: 16px 20px;
        resize: none;
        outline: none;
        &:hover {
            box-shadow: 0px 0px 4px 2px #E58D35;
        }
        &:focus {
            box-shadow: 0px 0px 4px 2px #89C249;
        }
        // &:focus-visible {

        // }
        &::placeholder {
            padding: 3px 0;
        }

        &::-webkit-scrollbar {
            width: 9px; /* Ширина скроллбара */
          }
          
        &::-webkit-scrollbar-thumb {
          background-color: $color-primary; /* Колір полоси скроллбара */
          border-radius: 6px; /* Радіус закруглення полоси скроллбара */
        }
        
        /* Задаємо відступи від верхнього, правого та нижнього краю скроллбара */
        &::-webkit-scrollbar-thumb {
          margin-top: 20px;
          margin-right: 10px;
          margin-bottom: 20px;
        }
        
        /* Стилізація скроллбара для інших браузерів */
        /* Firefox, Edge, Internet Explorer */
        & {
          scrollbar-width: thin;
          scrollbar-color: $color-primary transparent;
        }
    }


    .btn {
        width: 100%;
        position: relative;
    }

    .icon {
        position: absolute;
        right: 34px;
    }

}
