@media screen and (max-width: 1390px) {
    .slider-template {
        .slick-arrow {
            &.slick-prev {
                left: -60px;}
            &.slick-next {
                right: -60px;
            }
        }
    }
}

@media screen and (max-width: 1310px) {
    .slider-template {
        max-width: 750px;
        margin: 0 auto;
    }
}

@media screen and (max-width: 890px) {
    .slider-template {
        max-width: 390px;
    }
}

@media screen and (max-width: 1199px) {
    .calculation {
        &__img {
            width: 30vw;
        }
    }
}

@media screen and (max-width: 1208px) {
    .questions {
        .btn {
            padding-right: 19px;
        }
    }

    .cooperation {
        .img-fluid {
            max-width: 461px;
            width: 100%;
        }
    }
}

@media screen and (max-width: 1062px) {
    .questions {
        .icon {
            display: none;
        }
    }
}
